import React from 'react';
import './App.css';
import { AppBar, Paper, Fab, Snackbar, IconButton, Button, Tab, Tabs } from "@material-ui/core"
import { makeStyles, useTheme } from '@material-ui/core/styles';
import EmailIcon from '@material-ui/icons/Email';
import CloseIcon from '@material-ui/icons/Close';
import TabPanel from './TabPanel';
import SwipeableViews from 'react-swipeable-views';

function App() {

  const useStyles = makeStyles((theme) => ({
    tab: {
      backgroundColor: '#282c34',
      display: 'flex',
    },
    paper: {
      display: 'flex',
    }
  }));


  const classes = useStyles()
  const theme = useTheme();

  const [openNotify, setOpen] = React.useState(false);
  const handleOpenNotify = () => {
    setOpen(true);
  };
  const handleCloseNotify = (event, reason) => {
    if (reason === 'clickaway') { return; }
    setOpen(false);
  };

  const [tabVal, setTab] = React.useState(0)
  const tabChange = (event, newValue) => {
    setTab(newValue);
  };


  return (
    <div className="App">
      <AppBar position="static">
        <Tabs
          value={tabVal}
          onChange={tabChange}
          indicatorColor="primary"
          textColor="secondary"
          variant="fullWidth"
          aria-label="TODO">
            <Tab label="Home" value={0}/>
            <Tab label="Resume" value={1}/>
            <Tab label="About Me" value="2"/>
            <Tab label="Contact me" value="3"/>
        </Tabs>
      </AppBar>

      <TabPanel value={tabVal} index={0}>
        Item 0
        {/* <Paper className={classes.paper}>
          This is just a test
        </Paper> */}
      </TabPanel>

      
    
    <Fab color="primary" onClick={handleOpenNotify}>
      <EmailIcon />
    </Fab>
    <Snackbar
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'left',
        }}
        open={openNotify}
        autoHideDuration={6000}
        onClose={handleCloseNotify}
        message="Note archived"
        action={
          <React.Fragment>

            <IconButton size="small" aria-label="close" color="inherit" onClick={handleCloseNotify}>
              <CloseIcon fontSize="small" />
            </IconButton>
          </React.Fragment>
        }
      />
    </div>
  );
}

export default App;
